import * as React from 'react';
import { IconButton } from './Basics';
import type { IIconButtonProps } from './Basics';
import { IconRedo } from '../icons';

export function RedoButton(props: IIconButtonProps) {
  const {
    icon = <IconRedo />,
    ...rest
  } = props;

  return (
    <IconButton
      testid="redo_button"
      icon={icon}
      {...rest}
    />
  )
}